.App {
  text-align: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: justify;
      justify-content: space-between;
  height: 100vh;
  width: 100vw;
  -ms-flex-direction: column;
      flex-direction: column;
}

.App .part-input {
  font-family: Arial, Helvetica, sans-serif;
  font-weight: 600;
  margin: 10px;
  font-size: 14px;
  -webkit-transition: -webkit-transform 0.1s ease-out;
  transition: -webkit-transform 0.1s ease-out;
  -o-transition: transform 0.1s ease-out;
  transition: transform 0.1s ease-out;
  transition: transform 0.1s ease-out, -webkit-transform 0.1s ease-out;
  height: 35px;
}

.App .part-input:focus-within {
  -webkit-transform: scale(2);
      -ms-transform: scale(2);
          transform: scale(2);
}

.App .part-input__correct {
  color: green;
}

.App .part-input label {
  display: inline-block;
  width: 14px;
}

.App .part-input input {
  border: none;
  border-bottom: 2px solid black;
  width: 14px;
  margin-left: 1px;
  text-align: center;
  opacity: 0.5;
  -webkit-transition: opacity ease 0.2s;
  -o-transition: opacity ease 0.2s;
  transition: opacity ease 0.2s;
  outline: 0;

  color: transparent;
  text-shadow: 0 0 0 black;
}

.App .part-input input:focus {
  opacity: 1;
}

.App.all-correct .prize {
  height: 300px;
  -webkit-animation: prize-fadein 1s ease-in;
          animation: prize-fadein 1s ease-in;
}

.App .inputs {
  display: -ms-inline-flexbox;
  display: inline-flex;
  -ms-flex-positive: 0.5;
      flex-grow: 0.5;
}

@-webkit-keyframes prize-fadein {
  0% {
    opacity: 0;
    height: 0;
  }

  25% {
    opacity: 0;
    height: 300px;
  }

  100% {
    opacity: 1;
    height: 300px;
  }
}

@keyframes prize-fadein {
  0% {
    opacity: 0;
    height: 0;
  }

  25% {
    opacity: 0;
    height: 300px;
  }

  100% {
    opacity: 1;
    height: 300px;
  }
}


